html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", sans-serif;
}

#app {
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: center;
}

#header {
  width: 100%;
  padding: 1rem 2rem;
  background-color: #000;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}

#header > h1 {
  color: white;
  font-family: inherit;
  font-weight: 300;
}

#find {
  font-weight: 600;
}

#search {
  height: 2rem;
}

#search-box {
  height: 2rem;
  padding: .1rem .3rem;
  background-color: black;
  border: 1px solid white;
  color: white;
  margin-right: .5rem;
  box-sizing: border-box;
}

#search-box:focus {
  outline: none;
}

#search-button {
  height: 2rem;
  padding: .1rem .3rem;
  background-color: black;
  border: 1px solid white;
  color: white;
  font-family: inherit;
  font-weight: 500;
  cursor: pointer;
}

#search-button:focus {
  outline: none;
}

#results {
  width: 100%;
  padding: 2rem;
  padding-bottom: 0;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-start;
}

.result-item {
  width: 100%;
  margin-bottom: 1.5rem;
  padding: 1rem;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-start;
  align-items: center;
  box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.2);
  background-color: #fdfdfd;
  cursor: pointer;
}

.result-item > p {
  pointer-events: none;
}

.result-image {
  pointer-events: none;
  width: 10%;
  max-width: 3rem;
  margin-right: 5%;
  border: 1px solid black;
}

.result-title {
  width: 50%;
  margin-right: 5%;
}

.result-year {
  width: 30%;
}

#current-description {
  font-weight: bold;
  align-self: center;
  margin-bottom: 2rem;
  margin-left: 1rem;
  padding: 0;
}

#load {
  height: 2rem;
  align-self: center;
}

#labels {
  padding-left: 1rem;
  cursor: default;
  background: white;
  box-shadow: none;
}

#labels > .result-image {
  border: none;
}

#labels > h2 {
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 0;
}

@media screen and (max-width: 370px) {
  #labels > h2 {
    width: auto;
  }
}

nav {
  width: 100%;
  background-color: black;
  display: flex !important;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  padding-top: 1rem;
}

.page-item {
  background-color: black;
  color: white;
}

.page-link {
  background-color: black !important;
  border: 1px solid white !important;
  color: white !important;
}

.page-link:focus {
  background-color: black !important;
  border-color: white !important;
  color: white !important;
}

.page-link:hover {
  background-color: white !important;
  color: black !important;
}

.page-item.active .page-link {
  background-color: white !important;
  color: black !important;
  border-color: white !important;
}

#overlay {
  z-index: 9;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

#detailed-results {
  overflow-x: hidden;
  overflow-y: auto;
  position: fixed;
  top: 15vh;
  bottom: 15vh;
  width: 70vw;
  height: 70vh;
  padding-top: 7vh;
  background-color: white;
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
  background-color: #fdfdfd;
}

@media screen and (min-width: 720px) {
  #detailed-results {
    flex-flow: row nowrap;
  }
}

#detailed-results > img {
  pointer-events: none;
  height: 80%;
  margin: 1rem;
  margin-left: 7.5%;
  border: 1px solid black;
  box-sizing: content-box;
}

#detailed-info {
  pointer-events: none;
  padding: 1rem;
  padding-bottom: 2rem;
  margin-right: 2rem;
  display: flex;
  flex-flow: column nowrap;
  justify-content: flex-start;
  align-items: flex-start;
}

#detailed-info > h2 {
  pointer-events: none;
  font-size: 1.5rem;
  margin-bottom: 1rem;
  margin-top: 0;
}

#detailed-info > h3 {
  pointer-events: none;
  font-size: 1.25rem;
  margin-bottom: 1rem;
  margin-top: 0;
}

#detailed-info-list {
  pointer-events: none;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.detailed-info-item {
  margin-bottom: 1rem;
}

#exit-details {
  position: absolute;
  top: 0;
  right: 0;
  margin: 1rem;
  width: 1rem;
  cursor: pointer;
}